<template>
    <div>
        <el-form :inline=true :model="form" ref="form">
            <el-row :gutter="5">
                <el-col :span="20">
                    <el-form-item label="职工号:" label-width="68px">
                        <el-input v-model="form.studentid"></el-input>
                    </el-form-item>
                    <el-form-item label="姓名:" label-width="68px">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" size="mini" @click="searchTeacher">搜索</el-button>
                        <el-button size="mini" @click="resetFields('form')">重置</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <el-table v-loading="tableDataLoading" :data="tableData" border>
            <el-table-column prop="studentid" label="职工号" width="180">
            </el-table-column>
            <el-table-column prop="name" label="姓名" width="180">
            </el-table-column>
            <el-table-column prop="phone" label="手机号">
            </el-table-column>
            <el-table-column prop="dorm" label="宿舍">
            </el-table-column>
            <el-table-column prop="address" label="家庭住址">
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button size="small" type="primary" @click="showStudent(scope)" icon="el-icon-search" circle></el-button>
                    <el-button size="small" type="danger" @click="deleteStudent(scope)" icon="el-icon-delete" circle></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
import axios from "axios"

export default {
    name: "teacherList",
    data() {
        return {
            form: {
                studentid: "",
                name: "",
                classname: "",
            },
            classList: [],
            tableDataLoading: true,
            tableData: []
        }
    },
    mounted() {
        // 挂载
        this.getClassNameList();
        this.getStudentList();
    },
    methods: {
        searchStudent() {
            this.tableDataLoading = true;
            var formData = new FormData();
            formData.append("username", this.form.studentid);
            formData.append("name", this.form.name);
            axios
            .post("/user/searchTeacher", formData)
            .then(res => {
                this.tableDataLoading = false;
                var result = res.data;
                if( result.code == 200 ){
                    this.tableData = result.list;
                    return;
                }
                this.$message({
                    showClose: true,
                    message: '获取教师信息错误',
                    type: 'error'
                });
            })
            .catch(e=>{
                this.tableDataLoading = false;
                this.$message({
                    showClose: true,
                    message: '获取教师信息错误',
                    type: 'error'
                });
            })
        },
        getStudentList() {
            axios
            .get("/user/TeacherList")
            .then(res => {
                this.tableDataLoading = false;
                var result = res.data;
                if( result.code == 200 ){
                    this.tableData = result.list;
                    return;
                }
                this.$message({
                    showClose: true,
                    message: '获取教师信息错误',
                    type: 'error'
                });
            })
            .catch(e=>{
                this.tableDataLoading = false;
                this.$message({
                    showClose: true,
                    message: '获取教师信息错误',
                    type: 'error'
                });
            })
        },
        resetFields(form) {
            this.form.studentid = ""
            this.form.name = ""
            this.form.classname = ""
            this.form.classList = []
        },
        showStudent(scope) {
            console.log(scope.row)
        },
        deleteStudent(scope) {
            var studentid = scope.row.studentid;
            axios
            .delete("/user/deleteTeacher?username="+studentid)
            .then(res=>{
                console.log(res)
            })
        }
    }
}
</script>

<style scoped>
    .el-input {
        width: 12rem;
    }
</style>